			</el-form-item>
			
			<el-button @click.prevent="removeDomain(domain)" style="margin-left: 10px;" type="danger">删除</el-button>
		  </el-form-item>
		  <el-form-item>
			<el-button type="primary" @click="submitForm('dynamicValidateForm')">提交</el-button>
