						<span>
							{{item.chicang |thousands}} </br>
							{{item.keyong |thousands}}
						</span>
					</td>
					<td style="text-align: right;padding-right: 15px;">
						<span>
							{{item.chegnben |thousands}} </br>
							{{item.xianjia |thousands}}
						</span>
					</td>
				</tr>
			<!-- 	<tr v-for="(item,index) in dynamicValidateForm.domains" :key="index" :class="item.profit>0? 'red': 'biled'">
					<td style="padding: 5px 0 15px 0;">
						<span >
							<span>{{item.name}}</span> </br>
							{{item.money |thousands}}
						</span>
					</td>
					<td style="text-align: right;">
						<span>
							{{item.profit |thousands}} </br>
							{{item.proportion}}%
						</span>
					</td>
					<td style="text-align: right;">
						<span>
							{{item.chicang |thousands}} </br>
							{{item.keyong |thousands}}
						</span>
					</td>
					<td style="text-align: right;">
						<span>
							{{item.chegnben |thousands}} </br>
							{{item.xianjia |thousands}}
						</span>
					</td>
				</tr> -->
			</table>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="open=false">取 消</el-button>
		  </div>
		</el-dialog>
		
