<script>
	import '@/assets/fontStyle/index.css'
  export default {
	  filters:{
		  thousands(numer){
		        var dataval =Number(numer) 
				var data2 = dataval.toFixed(2).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g,'$&,');
				return  data2
		   }
	  },
    data() {
      return {
        dynamicValidateForm: {
          domains: [{
            name: '',
			profit:'',
			proportion:'',
			chicang:'',
			keyong:'',
			chegnben:'',
			xianjia:''
          }],
			money:0,
		   // domains: [{
					// name: "金桥信息",
					// money:417600.00,
					// profit:17825.30,
					// proportion:165124.600,
					// chicang:100.2,
					// keyong:100,
					// chegnben:11546500,
					// xianjia:100
		   // },
		   // {
					// name: "日播时尚",
					// money:0.00,
					// profit:-100,
					// proportion:-1654600,
					// chicang:-100,
					// keyong:-100,
					// chegnben:-11546500,
					// xianjia:-100
		   // }]
        },
		
	 validateTaskName:[
			{required: true, message: '盈亏不能为空', trigger: 'blur'},
			{
			   pattern: /^^(\-|\+)?\d+(\.\d+)?$/,
			   message: "输入正确的盈亏格式",
			   trigger: "blur"
			}
		],
	validateproportion:[
			{required: true, message: '盈亏不能为空', trigger: 'blur'},
			{
			   pattern: /^^(\-|\+)?\d+(\.\d+)?$/,
			   message: "输入正确的盈亏比格式",
			   trigger: "blur"
			}
		],
		open:false,
		
		
		ishow:true
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.open=true
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      removeDomain(item) {
        var index = this.dynamicValidateForm.domains.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.domains.splice(index, 1)
        }
      },
      addDomain() {
        this.dynamicValidateForm.domains.push({
          value: '',
          key: Date.now()
        });
      },
	  
    }
  }
</script>
